export default function LetsTalk() {
    const {t} = useTranslation();

    const [budget, setBudget] = useState("")
    const [companyName, setCompanyName] = useState("")
    const [country, setCountry] = useState("")
    const [email, setEmail] = useState("")
    const [interested, setInterested] = useState("")
    const [message, setMessage] = useState("")
    const [name, setName] = useState("")
    const [phoneNumber, setPhoneNumber] = useState("")
    const [website, setWebsite] = useState("")

    // const [submitForm, {loading}] = useMutation(SUBMIT_FORM);

    function handleSubmit(event) {
        event.preventDefault();
        // if (loading) return;
        //
        // submitForm({
        //     variables: {
        //         budget: budget,
        //         companyName: companyName,
        //         country: country,
        //         email: email,
        //         interested: interested,
        //         message: message,
        //         name: name,
        //         phoneNumber: phoneNumber,
        //         website: website,
        //     }
        // }).then((response) => {
        //     console.log('done', response);
        //     Array.from(document.querySelectorAll("input, textarea")).forEach(
        //         input => (input.value = "")
        //     );
        // }).catch(error => {
        //     console.error(error);
        // })
    }

    useLayoutEffect(() => {
        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            targets: '.container',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            scale: [0.8, 1],
            duration: 1500,
            delay: 500
        });
        timeline.add({
            targets: '.css--fadedtext',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            translateX: ["-100%", 0],
            duration: 500
        });
    }, [])

    return (
        <div className="relative pt-5 min-h-screen justify-center pb-36">
            <div className="container mx-auto px-4 h-full flex flex-col justify-center md:items-center pt-24">
                <h1 className="text-center text-2xl md:text-5xl glitch" data-glitch={t('contact_us')}>
                    {t('contact_us')}
                </h1>

                <div className="grid grid-cols-1 lg:grid-cols-5 w-full sm:pt-0 sm:-mx-4 md:py-4">
                    <div className="lg:col-span-2 px-4 w-full sm:mt-0 mb-8 lg:mb-0">
                        <div className="col-lg-4 mt-lg-0">
                            <div className="section css--letstalk-contacts p-4">
                                <h6 className="mb-3 text-white">{t('call_us')}</h6>
                                <p className="mb-0">+371 27 79 7931</p>
                                <p className="mb-4">+371 29 33 1911</p>
                                <p className="mb-4">+1 888 454 3088</p>
                                <h6 className="mb-3 text-white">{t('email_us')}</h6>
                                <p className="mb-0">info@kalmars.lv</p>
                                <p className="mb-4">it@kalmars.lv</p>
                                <h6 className="mb-3 text-white">{t('company')}</h6>
                                <p className="mb-0"> SIA Kalmars Group</p>
                                <p className="mb-4">Registration Nr. 40203267964</p>
                                <h6 className="mb-3 text-white">{t('adress')}</h6>
                                <p className="mb-4">Riga, Sarlotes iela 18A-3, LV-1001</p>
                                <p className="mb-4">Calgary, Alberta, T2P 2V6, Canada</p>
                                <h6 className="mb-3 text-white">{t('bank')}</h6>
                                <p className="mb-0">Bank account Nr. LV52HABA0551049691987</p>
                                <p className="mb-0">Bank: AS SWEDBANK</p>
                                <p className="mb-0">SWIFT code: HABALV22</p>
                                <p className="mb-0">VAT Nr: LV40203267964</p>
                            </div>
                        </div>
                    </div>
                    <div className="col-span-3 h-full hidden lg:block css--we-are-hero">
                        <div style={{width: "100%", height: "100%", position: "relative"}}>
                            <Canvas
                                className="css--contact-sphere"
                                gl={{alpha: true}}
                                camera={{position: [0, 0, 20], fov: 30}}>
                                <directionalLight intensity={1} position={[2, 2, 0]} color="#0E6373" distance={5} />
                                <OrbitControls makeDefault autoRotate autoRotateSpeed={0.3} enableZoom={false} zoomSpeed={0} />
                                <React.Suspense fallback={null}>
                                    <Model/>
                                </React.Suspense>
                            </Canvas>
                        </div>
                    </div>

                </div>
                <form method="post" onSubmit={handleSubmit} className="px-4 w-full pt-20 lg:w-2/3 mb-20 sm:mb-0">
                    <div className="grid xs:grid-cols-1 sm:grid-cols-2 gap-4">
                        <div>
                            <input className="css--form-style" name="name" type="text" placeholder={t('your_name')} required autoComplete="off" onChange={(e) => {
                                setName(e.target.value)
                            }}/>
                        </div>
                        <div>
                            <input className="css--form-style" name="email" type="email" placeholder={t('email')} required autoComplete="off" onChange={(e) => {
                                setEmail(e.target.value)
                            }}/>
                        </div>
                        <div>
                            <input className="css--form-style" name="phone" type="tel" placeholder={t('phone')} autoComplete="off" onChange={(e) => {
                                setPhoneNumber(e.target.value)
                            }}/>
                        </div>
                        <div>
                            <input className="css--form-style" name="country" type="text" placeholder={t('city')} autoComplete="off" onChange={(e) => {
                                setCountry(e.target.value)
                            }}/>
                        </div>
                        <div>
                            <select className="css--form-style" name="budget" onChange={(e) => {
                                setBudget(e.target.value)
                            }}>
                                <option data-display="Budget">{t('budget')}</option>
                                <option value="100-1000">&euro;100 - &euro;1000</option>
                                <option value="1000-5000">&euro;1000 - &euro;5000</option>
                                <option value="5000-10000">&euro;5000 - &euro;10000</option>
                                <option value="10000-50000">&euro;10000 - &euro;50000</option>
                                <option value="50000+">&euro;50000+</option>
                            </select>
                        </div>
                        <div>
                            <select className="css--form-style" name="interested" onChange={(e) => {
                                setInterested(e.target.value)
                            }}>
                                <option data-display="Interested For">{t('interested_for')}</option>
                                <option value="branding">{t('branding')}</option>
                                <option value="web-development">{t('web_dev')}</option>
                                <option value="kyc-aml">{t('kyc_aml')}</option>
                                <option value="saas-development">{t('saas_dev')}</option>
                                <option value="graphic-design">{t('design')}</option>
                            </select>
                        </div>
                        <div>
                            <input className="css--form-style" name="company" type="text" placeholder={t('company_name')} autoComplete="off" onChange={(e) => {
                                setCompanyName(e.target.value)
                            }}/>
                        </div>
                        <div>
                            <input className="css--form-style" name="website" type="text" placeholder={t('website')} autoComplete="off" onChange={(e) => {
                                setWebsite(e.target.value)
                            }}/>
                        </div>

                    </div>
                    <div className="grid grid-cols-1 gap-4 pt-4">
                        <textarea className="css--form-style" name="message" placeholder={t('tell_us_everything')} required onChange={(e) => {
                            setMessage(e.target.value)
                        }}/>
                    </div>
                    <div className="grid md:grid-cols-4 sm:grid-cols-1 my-4 text-center">
                        <button className="btn px-5" type="submit">{t('submit')}</button>
                    </div>
                </form>
            </div>
        </div>
    )
}
