import '../i18n/i18n';

import React from "react"
import FadedText from "../components/elements/FadedText";
import Navigation from "../components/layout/Navigation";
import LetsTalk from "../components/layout/ContactUs/LetsTalk";
import {SEO} from "../components/seo";

const ContactUs = () => {
    return (
        <div className="main-wrapper relative">
            <Navigation pageNr={'06'}/>
            <section>
                <FadedText text={'EXPAND'} direction="left"/>
                <LetsTalk/>
            </section>
        </div>
    )
}
export default ContactUs;
export const Head = () => <SEO title={"Contact Us"} description={'If you have any question or proposal, feel free to contact us!'}/>
